.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.form-container {
  border: 1px solid #ccc;
  padding: 20px;
  margin-bottom: 20px;
}

.form-container label {
  display: block;
  margin-bottom: 10px;
}

.form-container input[type="text"],
.form-container input[type="date"] {
  width: 95%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 20px;
}

.form-container button {
  background-color: #514e7f;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: 50%;
  max-width: 100px;
}

.form-container button:hover {
  background-color: #726eae;
}

.error-message {
  color: red;
  text-align: center;
  margin-bottom: 20px;
}

/* StockData.css */
.stock-data-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  max-width: 95%;
  margin-bottom: 3vh;
}

.heading {
  color: #514e7f;
  font-size: 40px;
  margin-bottom: 0;
}

.data-points {
  display: flex;
  justify-content: center;
}

.data-points p,
.stock-volume {
  font-size: 20px;
  margin-right: 0;
}
.data-points p strong,
.stock-volume strong {
  margin: 0 10px;
  font-size: 35px;
  color: #514e7f;
}
.disabled-content {
  pointer-events: none;
  opacity: 0.9;
  cursor: not-allowed;
}

/* Media query for mobile screens */
@media (max-width: 768px) {
  .form-container input[type="text"],
  .form-container input[type="date"] {
    padding: 2vw;
    font-size: 4vw;
  }
  .stock-data-container {
    flex-direction: column;
  }
  .heading {
    font-size: 9.5vw;
  }
  .data-points {
    flex-wrap: wrap;
  }
  .data-points p,
  .stock-volume {
    font-size: 4vw;
    /* text-align: start; //not required */
  }

  .data-points p strong,
  .stock-volume strong {
    font-size: 7vw;
  }
}
